@import url('https://fonts.googleapis.com/css2?family=Paytone+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');

/*DESKTOP*/
@media (min-width: 2000px) and (max-width: 2600px) {
    .containerAllPlan{
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 5%;
        margin-top: 80px;
    }
    .containerPlan{
        width: 326px;
        height: 373px;
        border-radius: 15px;
        background: #350D4A;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        transition: transform 0.5s ease;
    }
    .containerPlan:hover{
        transform: scale(1.1);
    }
    .titlePlan{
        color: white;
        width: 251px;
        height: 20px;
        font-size: 26px;
        font-family: 'Paytone One', sans-serif;
        text-align: center;
        margin: 70px auto 10px auto;
    }
    .textPlan{
        color: white;
        width: 251px;
        height: 60px;
        font-size: 18px;
        font-family: 'Oxygen', sans-serif;
        text-align: center;
        margin: 30px auto auto auto;
    }
    .buttonPlan{
        color: #6C2B8F;
        font-size: 20px;
        font-family: 'Paytone One', sans-serif;
        width: 200px;
        height: 40px;
        border: none;
        border-radius: 15px;
        background-color: white;
        transition: transform 0.5s ease;
        margin: auto auto 70px auto;
        cursor: pointer;
    }
    .buttonPlan:hover{
        transform: scale(1.1);
    }
}

