@import url('https://fonts.googleapis.com/css2?family=Paytone+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&display=swap');

/*DESKTOP*/
@media (min-width: 2000px) and (max-width: 2600px) {
    .planes{
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        margin-bottom: 120px;
    }
    .tituloPlanes{
        font-size: 74px;
        font-family: 'Paytone One', sans-serif;
        color: white;
        text-align: center;
        margin-top: 0px;
        margin-bottom: 0;
    }
    .colorPlanes{
        color: #6C2B8F;
    }
    .containerPlanes{
        display: flex;
        flex-direction: row; 
        justify-content: center;
        align-items: center; 
        height: 500px;
    }
    .containerTextPlanes{
        display: flex;
        flex-direction: row;  
    }
    .textPlanes{
        color: white;
        font-size: 45px;
        font-family: 'Oxygen', sans-serif;
        margin: 0;
    }
    .imgCheck{
        width: 47px;
        height: 47px;
        margin-right: 10px;
    }
    .laptop{
        background-image: url('../../images/laptopPlan.png');
        background-position: center;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        width: 700px;
        height: 700px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .textLaptop{
        color: #6a3a85e5;
        font-size: 70px;
        font-family: 'Zen Tokyo Zoo', cursive;
        font-weight: 100;
        text-shadow: 4px 4px 20px #938d97;
        margin-bottom: 90px;
    }
    .textLaptop{
        animation: oscilar 4s ease-in-out infinite;
    }
    
    @keyframes oscilar {
        0% {
          transform: scale(1);
        }
        50% {
          transform: scale(1.1);
        }
        100% {
          transform: scale(1);
        }
    }
    .buttonPlanes{
        width: 400px;
        height: 80px;
        border-radius: 15px;
        background-color: #A572C0;
        transition: transform 0.5s ease;
        color: black;
        font-family: 'Paytone One', sans-serif;
        font-size: 30px;
        border: none;
        cursor: pointer;
        margin: 50px auto auto auto;
    }
    .buttonPlanes:hover{
        transform: scale(1.1);
    }
    .pricePlanes{
        color: white;
        font-size: 25px;
        font-family: 'Oxygen', sans-serif;
        margin: 20px;
    }
}

